/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
  top: 0!important;
  cursor: pointer;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  top: -1px;
  left: -1px;
  min-width: 180px;
  z-index: 3000;
  padding-left: 20px;
  padding-right: 20px;
  background: #FFFFFF;
  border:1px solid #DAE0E6;
  border-radius: 2px;
  cursor: initial;
}

/* Links inside the dropdown */
.dropdown-content a {
  /**/
  padding-top: 5px;
  padding-bottom: 5px;
  text-decoration: none;
  display: block;
  font-size: 14px;
  color: #1A2736;
  letter-spacing: 0;
  line-height: 18px;
  cursor: pointer;
  /*white-space: nowrap;*/
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {color: #3C73B9 }

/* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
.show {display:block;}

