/* for sharp border. I draw this line by border instead of just 1px line. */
.full-page-table-body>.table-row.highlight::before {
  display: block;
  content: '';
  position: absolute;
  top: -7px;
  left: 0;
  height: 6px;
  width: 100%;
  background: linear-gradient(0deg, rgba(197, 219, 232, 0.4), rgba(255, 255, 255, 0.0));
  /*border-bottom: 1px solid #fff;*/
}

.full-page-table-body>.table-row.highlight::after {
  display: block;
  content: '';
  position: absolute;
  top: 49px;
  left: 0;
  height: 6px;
  width: 100%;
  background: linear-gradient(180deg, rgba(197, 219, 232, 0.4), rgba(255, 255, 255, 0.0));
  border-top: 1px solid #E3E8ED;
  border-bottom: none;
  z-index: 1; /* for over side page */
