
.process {
  width: 100%;
  position: relative;
}
.process .line {
  position: absolute;
  left: 6px;
  top: 6px;
  width: 1px;
  background-color: #C3D4E5;
}
.process .flow {
  width: 100%;
  position: relative;
  padding-left: 32px;
  margin: 20px 0;
}
.flow .flow-dot {
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  border: none;
  background-color: #007FDF;
  height: 12px;
  width: 12px;
  border-radius: 6px;
}
.flow .flow-detail {
  font-size: 14px;
  line-height: 20px;
}
.flow-detail .flow-info {
  position: relative;
  top: -4px;
}
.flow-detail .flow-area {
  position: relative;
  border: 1px solid #DAE0E6;
  border-radius: 2px;
  padding: 10px;
}
.flow-area .delete {
  background-image: url('~assets/delete.svg');
  width: 21px;
  height: 21px;
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
}
.final-operator {
  font-size: 14px;
  color: #5E6978;
  line-height: 20px;
  min-width: 260px;
}



